import React, { useState, useContext } from "react";
import Header from "./components/header";
import MainBody from "./components/body/MainBody";
import Modal from "./components/Modal/Modal";
import AuthContext from "./components/context/AuthContext";
import { getByTestId } from "@testing-library/react";
import classes from './App.module.css'
import Cart from "./components/Modal/Cart";

function App(props) {
  const initialCount = 0;
  const [sushiValue, setSushiValue] = useState(initialCount)
  const [schintzelValue, setSchintzelValue] = useState(initialCount)
  const [burgerValue, setBurgerValue] = useState(initialCount)
  const [greenValue, setGreenValue] = useState(initialCount)
  const [flag, setFlag] = useState(false)
  const [ids, setId] = useState('')
  const handleId = (id) => {
    if (id === 'sushi') {
      setSushiValue(sushiValue + 1)
    }
    else if (id === 'schintzel') {
      setSchintzelValue(schintzelValue + 1)
    }
    else if (id === 'barbecue') {
      setBurgerValue(burgerValue + 1)
    }
    else if (id === 'green') {
      setGreenValue(greenValue + 1)
    }
  }
  const reduseHandler = (id) => {
    if (id === 'sushi') {
      setSushiValue(sushiValue - 1)
    }
    else if (id === 'schintzel') {
      setSchintzelValue(schintzelValue - 1)
    }
    else if (id === 'barbecue') {
      setBurgerValue(burgerValue - 1)
    }
    else if (id === 'green') {
      setGreenValue(greenValue - 1)
    }
  }
  const handleFlag = () => {
    setFlag(true)
  }
  const handleFlagOut = () => {
    setFlag(false)
  }
  console.log(flag)
  return (
    <AuthContext.Provider
      value={{
        isFlag: flag,
        onFlagIn: handleFlag,
        onFlagOut: handleFlagOut,
        checkId: handleId,
        minus: reduseHandler,
        getSushiVlaue: sushiValue,
        getSchintzelValue: schintzelValue,
        getBurgervalue: burgerValue,
        getGreenValue: greenValue,
      }
      }
    >
      <div style={{ height: flag ? '100vh' : 'auto', overflow: flag ? 'hidden' : 'visible' }} className={classes.app}>

        <Header />
        <MainBody />
        <Modal />
        <Cart />
      </div>
    </AuthContext.Provider>

  );
}

export default App;
