import React, { useState, useContext } from "react";
import Classes from './MainMenu.module.css'
import AuthContext from "../context/AuthContext";

const MainMenu = (props) => {
  const initialCount = 0;
  const [sushiValue, setSushiValue] = useState(initialCount)
  const [schintzelValue, setSchintzelValue] = useState(initialCount)
  const [burgerValue, setBurgerValue] = useState(initialCount)
  const [greenValue, setGreenValue] = useState(initialCount)
  const ctx = useContext(AuthContext);

  const clickfunction = (e) => {
    e.preventDefault();
    let id = e.target.id

    ctx.checkId(id);
  }
  return (
    <div className={Classes.mainMenu}>
      <div className={Classes.mainMenudiv}>
        <div>
          <h1>Sushi</h1>
          <p>Fresh fish with vegetables</p>
          <p className={Classes.mainMenudivp}>$22.9</p>
        </div>
        <form ><div><label>Amount</label>
          <input type='number' value={parseInt(ctx.getSushiVlaue)} /></div>
          <button onClick={clickfunction} id='sushi' >+ Add</button>
        </form>
      </div>
      <div className={Classes.mainMenubr}></div>
      <div className={Classes.mainMenudiv}>
        <div>
          <h1>Schintzel</h1>
          <p>A german speciality</p>
          <p className={Classes.mainMenudivp}>$16.9</p>
        </div>
        <form ><div><label>Amount</label>
          <input type='number' value={ctx.getSchintzelValue} /> </div>
          <button onClick={clickfunction} id="schintzel">+ Add</button>
        </form>
      </div>
      <div className={Classes.mainMenubr}></div>
      <div className={Classes.mainMenudiv}>
        <div>
          <h1>Barbecue burger</h1>
          <p>American burger</p>
          <p className={Classes.mainMenudivp}>$12.9</p>
        </div>
        <form ><div><label>Amount</label>
          <input type='number' value={ctx.getBurgervalue} /></div>
          <button onClick={clickfunction} id="barbecue">+ Add</button>
        </form>
      </div>
      <div className={Classes.mainMenubr}></div>
      <div className={Classes.mainMenudiv}>
        <div>
          <h1>Green bowl</h1>
          <p>Healthy and green</p>
          <p className={Classes.mainMenudivp}>$22.9</p>
        </div>
        <form ><div><label>Amount</label>
          <input type='number' value={ctx.getGreenValue} /></div>
          <button onClick={clickfunction} id='green'>+ Add</button>
        </form>
      </div>
      <div className={Classes.mainMenubr}></div>
    </div>
  )
}
export default MainMenu;